<template>
  <div class="home-container">
    <!-- 导航栏 -->
    <van-nav-bar title="黑马头条" fixed />
    <van-pull-refresh v-model="refreshing" @refresh="onLoad(true)">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <!-- 文章列表 -->
        <Article v-for="(item, index) in articleList" :key="index" :title="item.title" :author="item.aut_name" :cmt-count="item.comm_count" :cover="item.cover"></Article>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { ArticleAPI } from '@/api/ArticleAPI/ArticleAPI.js'
import Article from '@/components/Article/Article.vue'

export default {
  name: 'Home',
  components: {
    Article
  },
  data() {
    return {
      page: 1,
      limit: 10,
      articleList: [],
      loading: true, // 避免第一次打开页面就触发上拉加载
      finished: false,
      refreshing: false
    }
  },
  created() {
    this.initArticleList()
  },
  methods: {
    async initArticleList(isRefresh) {
      // 获取文章列表
      const { data: res } = await ArticleAPI(this.page, this.limit)
      if (res.length === 0) {
        // 如果没有数据则取消加载功能
        this.finished = true
      }
      if (isRefresh) {
        // 下拉刷新，新的数据要放在前面
        this.articleList = [...res, ...this.articleList]
      } else {
        // 上拉加载，数据要放在后面
        this.articleList = [...this.articleList, ...res]
      }
      this.loading = false
      this.refreshing = false
    },
    onLoad(isRefresh) {
      // 用 isRefresh 参数来判断是否是下拉刷新操作
      // 若传入参数则是下拉刷新，若没传参数则是上拉加载
      this.page++
      this.initArticleList(isRefresh)
    }
  }
}
</script>

<style lang="less" scoped>
.home-container {
  padding: 46px 0 50px 0;
}
</style>
